
import './main.scss';


$( function() {
  $.extend($.datepicker,{_checkOffset:function(inst,offset,isFixed){return offset}});
  $( "#from, #to" ).datepicker({
    showOn: "button",
    buttonText: "date",
    buttonImage: "https://www.pngmart.com/files/15/Arrow-Down-Transparent-Images-PNG.png",
    buttonImageOnly: true,
    showOtherMonths: true,
    selectOtherMonths: true,
    showButtonPanel: true,
  });
    
  $.datepicker.regional['ru'] = {
    closeText: '–ó–∞–∫—Ä—ã—Ç—å',
    prevText: '–ü—Ä–µ–¥—ã–¥—É—â–∏–π',
    nextText: '–°–ª–µ–¥—É—é—â–∏–π',
    currentText: '–°–µ–≥–æ–¥–Ω—è',
    monthNames: ['–Ø–Ω–≤–∞—Ä—å','–§–µ–≤—Ä–∞–ª—å','–ú–∞—Ä—Ç','–ê–ø—Ä–µ–ª—å','–ú–∞–π','–ò—é–Ω—å','–ò—é–ª—å','–ê–≤–≥—É—Å—Ç','–°–µ–Ω—Ç—è–±—Ä—å','–û–∫—Ç—è–±—Ä—å','–ù–æ—è–±—Ä—å','–î–µ–∫–∞–±—Ä—å'],
    monthNamesShort: ['–Ø–Ω–≤','–§–µ–≤','–ú–∞—Ä','–ê–ø—Ä','–ú–∞–π','–ò—é–Ω','–ò—é–ª','–ê–≤–≥','–°–µ–Ω','–û–∫—Ç','–ù–æ—è','–î–µ–∫'],
    dayNames: ['–≤–æ—Å–∫—Ä–µ—Å–µ–Ω—å–µ','–ø–æ–Ω–µ–¥–µ–ª—å–Ω–∏–∫','–≤—Ç–æ—Ä–Ω–∏–∫','—Å—Ä–µ–¥–∞','—á–µ—Ç–≤–µ—Ä–≥','–ø—è—Ç–Ω–∏—Ü–∞','—Å—É–±–±–æ—Ç–∞'],
    dayNamesShort: ['–≤—Å–∫','–ø–Ω–¥','–≤—Ç—Ä','—Å—Ä–¥','—á—Ç–≤','–ø—Ç–Ω','—Å–±—Ç'],
    dayNamesMin: ['–í—Å','–ü–Ω','–í—Ç','–°—Ä','–ß—Ç','–ü—Ç','–°–±'],
    weekHeader: '–ù–µ',
    dateFormat: 'dd.mm.yy',
    firstDay: 1,
    isRTL: false,
    showMonthAfterYear: false,
    yearSuffix: ''
  };
    
    
  var dateFormat = "dd.mm.yy",
    from = $( "#from" )
    .datepicker({
      defaultDate: "+1w",
      changeMonth: true,
      numberOfMonths: 3
    })
    .on( "change", function() {
      to.datepicker( "option", "minDate", getDate( this ) );
    }),
    to = $( "#to" ).datepicker({
      defaultDate: "+1w",
      changeMonth: true,
      numberOfMonths: 3
    })
    .on( "change", function() {
    from.datepicker( "option", "maxDate", getDate( this ) );
  });

  function getDate( element ) {
    var date;
    try {
      date = $.datepicker.parseDate( dateFormat, element.value );
    } catch( error ) {
      date = null;
    }

    return date;
  }
  
  
  $.datepicker.setDefaults($.datepicker.regional['ru']);
    
} );